<template>
    <div>
        <el-container>
            <el-header>
                Header
                <el-menu
                    :default-active="activeIndex"
                    class="el-menu-demo"
                    mode="horizontal"
                    @select="handleSelect"
                    background-color="#545c64"
                    text-color="#fff"
                    active-text-color="#ffd04b">
                    <el-submenu index="1">
                        <template slot="title">我的工作台</template>
                        <el-menu-item index="1-1">选项1</el-menu-item>
                        <el-menu-item index="1-2">选项2</el-menu-item>
                        <el-menu-item index="1-3">选项3</el-menu-item>
                        <el-submenu index="1-4">
                            <template slot="title">选项4</template>
                            <el-menu-item index="1-4-1">选项1</el-menu-item>
                            <el-menu-item index="1-4-2">选项2</el-menu-item>
                            <el-menu-item index="1-4-3">选项3</el-menu-item>
                        </el-submenu>
                    </el-submenu>
                </el-menu>
            </el-header>
            <h2>{{msg}}</h2>
            <el-footer>Footer</el-footer>
        </el-container>
    </div>
</template>

<script>
export default {
    name:'comHMF',
    data() {
      return {
        activeIndex: '1',
        msg:'',
      };
    },
    methods: {
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
        if (key == '1-1') {
            this.msg = '1-1';
        }else if(key == '1-2'){
            this.msg = '1-2';
        }else if(key == '1-3'){
            this.msg = '1-3';
        }
      }
    },
}
</script>

<style>
.el-container{
    padding: 50px 0;
}
.el-header, .el-footer {
    position: fixed;
    background-color: #B3C0D1;
    color: #333;
    text-align: center;
    line-height: 60px;
    z-index: 1000;
  }
  .el-header{
      top: 0;
      width: 100%;
  }
  .el-footer{
    bottom: 0px;
    width: 100%;
  }
  .el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    line-height: 160px;
  }
  body .el-container {
    margin-bottom: 40px;
  }
  .el-menu{
      margin-left: -20px;
      width: 130px;
  }
</style>



